import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { IonicPage, NavController, NavOptions, AlertController, ToastController, MenuController, NavParams, ModalController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
import { NewModalPage } from '../new-modal/new-modal';

@IonicPage({
  name: 'page-auth2',
  segment: 'auth2',
  priority: 'high'
})

@Component({
  selector: 'page-auth2',
  templateUrl: 'auth-2.html'
})
export class AuthPage2 implements OnInit {
  public onLoginForm: FormGroup;
  public onRegisterForm: FormGroup;
  auth: string = "register";
  tags: string;
  tag: string;
  tags1 = [];
  tags2 = [];
  arr = [];
  str: string;
  disable: boolean = true;
  disable1: boolean = false;
  counter: number = 0;
  backspace: boolean = false;
  show: boolean = false;
  newWord: string;
  email: any;
  password: any;
  country: any;
  toggleInstructions: boolean = false;
  toggleExample: boolean = false;
  newScopeVariable: number = 0;
  public PASSWORD_REGEX = new RegExp('^[A-Za-z0-9? ]+$');
  toggleHelp: boolean = false;
  toggleLogin: boolean = true;
  toggleLoginBtn: boolean = false;
  modalDismissData: any;


  constructor(private _fb: FormBuilder, public translate: TranslateService, public navparams: NavParams,
    public nav: NavController, public forgotCtrl: AlertController, public menu: MenuController,
    public toastCtrl: ToastController, private storage: Storage, public modalCtrl: ModalController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.menu.swipeEnable(false);
    this.menu.enable(false);
    this.storage = storage;
    this.email = this.navparams.get('email');
    this.password = this.navparams.get('password');
    this.country = this.navparams.get('country');



  }

  checkEnter(event: any) {
    if (this.a11yClick(event)) {
      this.updateList(event);
    }
  }


  a11yClick(event: any) {

    var code: any = event.charCode || event.keyCode;
    code = event.charCode || event.keyCode;
    if ((code === 32) || (code === 13)) {
      return true;
    }
  }





  openModal() {
    var msg: string = "resgister-pag-2.paragraph1";

    this.translate.get(msg).subscribe(async (translated: string) => {
      var content = this.translate.instant(translated);
      const helpModal = this.modalCtrl.create(NewModalPage, { html: content });
      await helpModal.present();
    });
  }

  showHelp(fab: any) {
    fab.close();
    this.openModal();
  }

  getRandomColor() {

    let color = this.randomizeColor();

    let c = color.substring(1);      // strip #
    let rgb = parseInt(c, 16);   // convert rrggbb to decimal
    let r = (rgb >> 16) & 0xff;  // extract red
    let g = (rgb >> 8) & 0xff;  // extract green
    let b = (rgb >> 0) & 0xff;  // extract blue

    let luma = 0.2126 * r + 0.7152 * g + 0.0722 * b; // per ITU-R BT.709

    if (luma < 40) {
      this.getRandomColor();
      return;
    } else {

      return {
        background: color
      }

    }

  }

  randomizeColor() {

    var color = "#";
    for (var i = 0; i < 3; i++) {
      var part = Math.round(Math.random() * 255).toString(16);
      color += (part.length > 1) ? part : "0" + part;
    }
    return color;

  }

  removeTag(tagId) {

    this.arr.splice(tagId, 1);
    return;
  }


  updateList(event) {

    if (this.newWord === null || this.newWord == undefined) {
      return;
    }

    this.newWord = this.newWord.trim();
    //protecting newValue null pointer


    let regExp = new RegExp(/^[a-zA-Z0-9]{1,19}$/);

    if (!regExp.test(this.newWord)) {

      this.translate.get('resgister-pag-2.error_length').subscribe((translated: string) => {
        var toast1 = this.translate.instant('Words can be made up of English alphabet letters and numbers and must be 3 to 19 characters long.');
        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'

        });
        toast.present();
      });

      this.newWord = this.newWord.slice(0, -1);
      return false;
    }

    if (this.arr.length == 12) {

      this.translate.get('resgister-pag-2.error_count').subscribe((translated: string) => {
        var toast1 = this.translate.instant('resgister-pag-2.error_count');
        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'

        });
        toast.present();
      });


      return false;
    }

    //add item to array once space is pressed
    regExp = new RegExp(/[`~!@#$%^&*()_|+\-=?;:",.<>\s\{\}\[\]\\\/\-]/gi);
    let outString = this.newWord.replace(regExp, '');

    // It has any kind of whitespace
    this.arr.push(outString);

    this.newWord = null;

    return;
  }


  ngOnInit() {

  }
  // check tags and go to next tag page
  checkTag() {


    // console.log(this.tags1.sort());
    this.arr = this.arr.filter(function (v) { return v !== '' });

    if (this.arr.length >= 9 && this.arr.length <= 12) {
      this.nav.push('page-auth3', { 'tags': this.arr, 'count': this.arr.length, 'country': this.country, 'email': this.email, 'password': this.password });

    } else {

      this.translate.get('resgister-pag-2.error_toast2').subscribe((translated: string) => {
        var toast2 = this.translate.instant('resgister-pag-2.error_toast2');
        this.tags = '';
        let toast = this.toastCtrl.create({
          message: toast2,
          duration: 3000,
          position: 'top'

        });

        toast.onDidDismiss(() => {
        });

        toast.present();
      })
    }

  }


}



